.App {
  text-align: center;
}

button {
  background-color: rgba(255, 146, 204, 0.9);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  margin: 6px;
}

button:hover {
  background-color: deeppink; 
  transform: translateY(-3px); 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

div {
  box-sizing: border-box;
}
.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}

.modal {
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}

.walking {
  margin-top: 20px;
  padding: 20px;
  background: pink;
  border: #61dafb solid 1px;
  border-radius: 8px;
  box-shadow: 0 4px 8px black;
  animation: fadeInOut 4s ease-in-out infinite;;
}

@keyframes fadeInOut{
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}



.black-nav {
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
